:root {
  --background-dark: #0a0c17;
  --background-light: #f8fafc;
  --card-dark: #121828;
  --card-light: #ffffff;
  --card-border-dark: #1d2a4a;
  --card-border-light: #e2e8f0;
  --text-primary-dark: #f1f5f9;
  --text-primary-light: #1e293b;
  --text-secondary-dark: #94a3b8;
  --text-secondary-light: #64748b;
  --text-tertiary-dark: #64748b;
  --text-tertiary-light: #94a3b8;
  --accent-blue: #2563eb;
  --accent-purple: #7c3aed;
  --accent-teal: #0ea5e9;
  --accent-green: #10b981;
  --accent-red: #ef4444;
  --gradient-primary: linear-gradient(135deg, var(--accent-blue), var(--accent-purple));
  --gradient-secondary: linear-gradient(135deg, var(--accent-teal), var(--accent-green));
  --glow-dark: 0 0 15px rgba(59, 130, 246, 0.4);
  --glow-light: 0 0 10px rgba(37, 99, 235, 0.1);
  --transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  --border-radius: 16px;
  --up-color: #10b981;
  --down-color: #ef4444;
  
  
  /* Current theme variables */
  --background: var(--background-dark);
  --card-bg: var(--card-dark);
  --card-border: var(--card-border-dark);
  --text-primary: var(--text-primary-dark);
  --text-secondary: var(--text-secondary-dark);
  --text-tertiary: var(--text-tertiary-dark);
  --glow: var(--glow-dark);
  --header-bg: rgba(10, 12, 23, 0.95);
  --card-hover-bg: rgba(30, 41, 59, 0.6);
  --footer-bg: rgba(10, 12, 23, 0.95);
  --button-secondary-bg: rgba(18, 24, 40, 0.8);
  --button-secondary-hover: rgba(30, 41, 59, 0.8);
  --stats-bg: rgba(30, 41, 59, 0.6);
  --social-icon-bg: rgba(30, 41, 59, 0.8);
  --progress-bg: rgba(30, 41, 59, 0.8);
  --radial-gradient: radial-gradient(circle at 10% 20%, rgba(37, 99, 235, 0.1) 0%, transparent 20%),
              radial-gradient(circle at 90% 80%, rgba(124, 58, 237, 0.1) 0%, transparent 20%);
}

body.light-mode {
  --background: var(--background-light);
  --card-bg: var(--card-light);
  --card-border: var(--card-border-light);
  --text-primary: var(--text-primary-light);
  --text-secondary: var(--text-secondary-light);
  --text-tertiary: var(--text-tertiary-light);
  --glow: var(--glow-light);
  --header-bg: rgba(248, 250, 252, 0.95);
  --card-hover-bg: rgba(226, 232, 240, 0.4);
  --footer-bg: rgba(248, 250, 252, 0.95);
  --button-secondary-bg: rgba(226, 232, 240, 0.6);
  --button-secondary-hover: rgba(203, 213, 225, 0.7);
  --stats-bg: rgba(226, 232, 240, 0.4);
  --social-icon-bg: rgba(226, 232, 240, 0.6);
  --progress-bg: rgba(226, 232, 240, 0.6);
  --radial-gradient: radial-gradient(circle at 10% 20%, rgba(37, 99, 235, 0.05) 0%, transparent 20%),
              radial-gradient(circle at 90% 80%, rgba(124, 58, 237, 0.05) 0%, transparent 20%);
  --up-color: #00c853;
  --down-color: #ff3d00;
}