/* Trending Page Styles */
:root {
  --background-dark: #0a0c17;
  --card-dark: #121828;
  --card-border: #1d2a4a;
  --accent-blue: #2563eb;
  --accent-purple: #7c3aed;
  --accent-teal: #0ea5e9;
  --accent-green: #10b981;
  --accent-pink: #ff7bc2;
  --accent-red: #ef4444;
  --text-primary: #f1f5f9;
  --text-secondary: #94a3b8;
  --text-tertiary: #64748b;
  --glow: 0 0 15px rgba(59, 130, 246, 0.4);
  --transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  --border-radius: 16px;
  --up-color: #10b981;
  --down-color: #ef4444;
  --radial-gradient: radial-gradient(circle at 10% 20%, rgba(37, 99, 235, 0.1) 0%, transparent 20%),
                    radial-gradient(circle at 90% 80%, rgba(124, 58, 237, 0.1) 0%, transparent 20%);
}

.light-theme {
  --background-dark: #f0f4f9;
  --card-dark: #ffffff;
  --card-border: #d0d8e8;
  --accent-blue: #0089ec;
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --glow: 0 0 15px rgba(0, 137, 236, 0.2);
}

/* Trending Page Container */
.trending-page {
  min-height: 100vh;
  background-color: var(--background-dark);
  color: var(--text-primary);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.6;
  transition: var(--transition);
  overflow-x: hidden;
  padding: 15px;
}

.trending-page::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--radial-gradient);
  z-index: -1;
  pointer-events: none;
}

.trending-page .container {
  max-width: 1920px;
  margin: 0 auto;
  padding: 20px;
  padding-top: 100px; /* Ensure content starts below header */
  padding-bottom: 80px; /* 2x footer height for proper spacing */
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  height: calc(100vh - 280px); /* Account for header and 2x footer height */
}

/* Custom Scrollbar */
.trending-page ::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

.trending-page ::-webkit-scrollbar-track {
  background: rgba(30, 41, 59, 0.3);
  border-radius: 5px;
}

.trending-page ::-webkit-scrollbar-thumb {
  background: var(--accent-blue);
  border-radius: 5px;
}

.trending-page ::-webkit-scrollbar-thumb:hover {
  background: var(--accent-purple);
}

/* Trending Section */
.trending-section {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}

.section-title {
  font-size: 1.8rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 12px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section-subtitle {
  color: var(--text-secondary);
  font-size: 1.1rem;
  margin-top: 8px;
}

.view-options {
  display: flex;
  gap: 12px;
}

.view-btn {
  padding: 8px 16px;
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  color: var(--text-secondary);
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 8px;
}

.view-btn.active, .view-btn:hover {
  background: var(--accent-blue);
  color: white;
  border-color: var(--accent-blue);
}

.trending-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.trending-header {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
  padding: 0 20px;
  color: var(--text-secondary);
  font-weight: 500;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--card-border);
}

.header-item {
  display: flex;
  align-items: center;
  gap: 8px;
}

.trending-list {
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding-right: 5px;
}

.trending-token {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  padding: 16px;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
  align-items: center;
  transition: var(--transition);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.trending-token:hover {
  border-color: var(--accent-blue);
  transform: translateY(-2px);
  box-shadow: var(--glow);
}

.trending-token::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 4px;
  background: linear-gradient(to bottom, var(--accent-blue), var(--accent-purple));
}

.token-info {
  display: flex;
  align-items: center;
  gap: 15px;
}

.token-logo {
  width: 45px;
  height: 45px;
  border-radius: 12px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 1.1rem;
  flex-shrink: 0;
  position: relative;
}

.token-logo::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 2px solid transparent;
  border-radius: 12px;
  background: linear-gradient(45deg, var(--accent-blue), var(--accent-purple)) border-box;
  -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
}

.token-details {
  display: flex;
  flex-direction: column;
}

.token-name {
  font-weight: 600;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-primary);
}

.token-symbol {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.token-migration {
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 500;
}

.token-migration.migrated {
  background: rgba(16, 185, 129, 0.2);
  color: var(--accent-green);
}

.token-migration.migrating {
  background: rgba(245, 158, 11, 0.2);
  color: #f59e0b;
}

.token-price {
  display: flex;
  flex-direction: column;
}

.price-value {
  font-weight: 700;
  font-size: 1.1rem;
  color: var(--text-primary);
}

.price-change {
  display: inline-block;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 600;
  margin-top: 4px;
}

.price-change.positive {
  background: rgba(16, 185, 129, 0.2);
  color: var(--accent-green);
}

.price-change.negative {
  background: rgba(239, 68, 68, 0.2);
  color: var(--accent-red);
}

.token-volume {
  font-weight: 500;
  color: var(--text-primary);
}

.token-liquidity {
  display: flex;
  flex-direction: column;
}

.liquidity-value {
  font-weight: 700;
  color: var(--text-primary);
}

.liquidity-bar {
  width: 100%;
  height: 6px;
  background: rgba(30, 41, 59, 0.6);
  border-radius: 3px;
  margin-top: 6px;
  overflow: hidden;
}

.liquidity-fill {
  height: 100%;
  border-radius: 3px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
}

.token-actions {
  display: flex;
  gap: 10px;
}

.action-btn {
  padding: 8px 16px;
  border-radius: 10px;
  font-weight: 500;
  font-size: 0.9rem;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 6px;
  border: none;
}

.view-btn {
  background: rgba(37, 99, 235, 0.1);
  color: var(--accent-blue);
  border: 1px solid var(--card-border);
}

.view-btn:hover {
  background: rgba(37, 99, 235, 0.2);
  border-color: var(--accent-blue);
}

.trade-btn {
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  color: white;
}

.trade-btn:hover {
  transform: translateY(-1px);
  box-shadow: var(--glow);
}

.highlight {
  animation: highlight 2s ease;
}

@keyframes highlight {
  0% { background-color: rgba(37, 99, 235, 0.2); }
  100% { background-color: transparent; }
}

.filters-container {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.filter-group {
  display: flex;
  gap: 10px;
  align-items: center;
}

.filter-label {
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.filter-select {
  padding: 8px 16px;
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  color: var(--text-primary);
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
}

.filter-select:hover {
  border-color: var(--accent-blue);
}

.search-container {
  position: relative;
  max-width: 300px;
}

.search-container input {
  width: 100%;
  padding: 10px 15px 10px 40px;
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  border-radius: 30px;
  color: var(--text-primary);
  font-size: 1rem;
  transition: var(--transition);
}

.search-container input:focus {
  outline: none;
  border-color: var(--accent-blue);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.3);
}

.search-icon {
  position: absolute;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--text-secondary);
}

.trending-stats {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-bottom: 20px;
}

.stat-card {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  padding: 20px;
  transition: var(--transition);
}

.stat-card:hover {
  border-color: var(--accent-blue);
  transform: translateY(-2px);
  box-shadow: var(--glow);
}

.stat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.stat-title {
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.stat-icon {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  background: linear-gradient(45deg, var(--accent-blue), var(--accent-purple));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.stat-value {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--text-primary);
}

.stat-change {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-top: 8px;
  font-size: 0.9rem;
}

.stat-change.positive {
  color: var(--accent-green);
}

.stat-change.negative {
  color: var(--accent-red);
}

.stat-change i {
  font-size: 0.8rem;
}

/* Responsive Design */
@media (max-width: 1400px) {
  .trending-header, .trending-token {
    grid-template-columns: 2fr 1fr 1fr 1fr;
  }
}

@media (max-width: 1200px) {
  .trending-header, .trending-token {
    grid-template-columns: 2fr 1fr 1fr;
  }
}

@media (max-width: 992px) {
  .trending-header, .trending-token {
    grid-template-columns: 2fr 1fr;
  }
}

@media (max-width: 768px) {
  .trending-header, .trending-token {
    grid-template-columns: 1fr 1fr;
  }
  
  .section-title {
    font-size: 1.5rem;
  }
  
  .trending-stats {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .trending-page .container {
    padding: 15px;
    padding-top: 100px; /* Ensure content starts below header on mobile */
    padding-bottom: 80px; /* 2x footer height for proper spacing on mobile */
  }
}

@media (max-width: 576px) {
  .trending-header, .trending-token {
    grid-template-columns: 1fr;
    gap: 10px;
  }
  
  .token-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .token-actions {
    width: 100%;
    justify-content: space-between;
  }
  
  .trending-stats {
    grid-template-columns: 1fr;
  }
  
  .trending-page {
    padding: 10px;
  }
  
  .trending-page .container {
    padding-top: 100px; /* Ensure content starts below header on mobile */
    padding-bottom: 80px; /* 2x footer height for proper spacing on mobile */
  }
}
