.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 30px 20px 80px; /* 2x footer height for proper spacing */
  padding-top: 100px; /* Ensure content starts below header */
}

.trending-carousel {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--card-border);
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 30px;
  overflow: hidden;
  position: relative;
  background-color: var(--card-bg);
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.carousel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.section-title {
  font-size: 1.4rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--text-primary);
}

.section-title i {
  color: var(--accent-blue);
}

.carousel-nav {
  display: flex;
  gap: 10px;
}

.nav-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--button-secondary-bg);
  border: 1px solid var(--card-border);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition);
  color: var(--text-primary);
}

.nav-btn:hover {
  background: var(--accent-blue);
  border-color: var(--accent-blue);
  color: white;
}

.trending-tokens {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  padding-bottom: 10px;
  scrollbar-width: thin;
  scrollbar-color: var(--accent-blue) transparent;
}

.trending-tokens::-webkit-scrollbar {
  height: 6px;
}

.trending-tokens::-webkit-scrollbar-thumb {
  background: var(--accent-blue);
  border-radius: 3px;
}

.trending-token {
  min-width: 280px;
  background: var(--card-hover-bg);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 16px;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: var(--transition);
}

.trending-token:hover {
  border-color: var(--accent-blue);
  transform: translateY(-3px);
  box-shadow: var(--glow);
}

.trending-token-logo {
  width: 50px;
  height: 50px;
  border-radius: 12px;
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.2rem;
  color: white;
  flex-shrink: 0;
}

.trending-token-info {
  flex: 1;
}

.trending-token-name {
  font-weight: 600;
  margin-bottom: 4px;
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-primary);
}

.trending-token-symbol {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.trending-token-price {
  font-weight: 700;
  font-size: 1.1rem;
  margin-top: 5px;
  color: var(--accent-green);
}

.price-change {
  display: inline-block;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 600;
  margin-left: 8px;
}

.price-change.positive {
  background: rgba(16, 185, 129, 0.2);
  color: var(--accent-green);
}

.price-change.negative {
  background: rgba(239, 68, 68, 0.2);
  color: var(--accent-red);
}

.token-grid-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 40px 0 20px;
}

.filter-buttons {
  display: flex;
  gap: 12px;
}

.filter-btn {
  padding: 8px 16px;
  background: var(--button-secondary-bg);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  color: var(--text-secondary);
  font-weight: 500;
  transition: var(--transition);
}

.filter-btn.active, .filter-btn:hover {
  background: var(--accent-blue);
  color: white;
  border-color: var(--accent-blue);
}

.token-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
}

.token-card {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 16px;
  overflow: hidden;
  transition: var(--transition);
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.token-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--glow);
  border-color: var(--accent-blue);
}

.token-card-header {
  padding: 20px 20px 15px;
  display: flex;
  flex-direction: column;
  position: relative;
  border-bottom: 1px solid var(--card-border);
  gap: 15px;
}

.token-logo {
  width: 60px;
  height: 60px;
  border-radius: 14px;
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 1.5rem;
  flex-shrink: 0;
  overflow: hidden;
  position: relative;
}

.token-logo::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 2px solid transparent;
  border-radius: 14px;
  background: linear-gradient(45deg, var(--accent-blue), var(--accent-purple)) border-box;
  -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
}

.token-header-main {
  display: flex;
  align-items: center;
  gap: 15px;
}

.token-details {
  flex-grow: 1;
  overflow: hidden;
}

.token-name {
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 4px;
  display: flex;
  align-items: center;
  gap: 8px;
  line-height: 1.2;
}

.token-symbol {
  font-size: 0.95rem;
  color: var(--text-secondary);
  font-weight: 500;
  margin-bottom: 0;
  line-height: 1.2;
}

.migration-status-corner {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 10;
}

.migration-status {
  display: inline-block;
  padding: 4px 10px;
  background: rgba(16, 185, 129, 0.2);
  color: var(--accent-green);
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
}

.migration-status.migrating {
  background: rgba(59, 130, 246, 0.2);
  color: var(--accent-blue);
}

.social-icons {
  display: flex;
  gap: 8px;
  justify-content: flex-start;
  margin-top: 5px;
}

.social-icon {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition);
  color: var(--text-primary);
  font-size: 0.9rem;
  background: var(--social-icon-bg);
  border: 1px solid var(--card-border);
}

.social-icon:hover {
  transform: translateY(-2px);
  background: var(--accent-blue);
  border-color: var(--accent-blue);
  color: white;
}

.token-card-body {
  padding: 20px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.description {
  color: var(--text-secondary);
  font-size: 0.95rem;
  line-height: 1.6;
  margin-bottom: 20px;
  position: relative;
  max-height: 4.5em;
  overflow: hidden;
}

.description.expanded {
  max-height: none;
}

.read-more {
  color: var(--accent-blue);
  font-size: 0.9rem;
  font-weight: 500;
  margin-top: 8px;
  display: inline-block;
}

.read-more:hover {
  text-decoration: underline;
}

.stats-container {
  background: var(--stats-bg);
  border-radius: 12px;
  padding: 16px;
  margin-bottom: 20px;
  border: 1px solid var(--card-border);
}

.stat-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 14px;
}

.stat-row:last-child {
  margin-bottom: 0;
}

.stat-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 6px;
}

.stat-value {
  color: var(--text-primary);
  font-size: 0.95rem;
  font-weight: 600;
  text-align: right;
}

.progress-container {
  margin-top: 15px;
}

.progress-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.progress-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.progress-value {
  color: var(--text-primary);
  font-size: 0.9rem;
  font-weight: 600;
}

.progress-bar {
  height: 8px;
  background: var(--progress-bg);
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}

.progress-fill {
  height: 100%;
  background: var(--gradient-secondary);
  border-radius: 4px;
  width: 68%;
  position: relative;
}

.progress-fill.migrated {
  width: 100%;
  background: var(--accent-green);
}

.token-card-footer {
  padding: 0 20px 20px;
}

.action-buttons {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.action-btn {
  flex: 1;
  min-width: 100px;
  height: 40px;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 0.9rem;
  transition: var(--transition);
  background: var(--button-secondary-bg);
  border: 1px solid var(--card-border);
  color: var(--text-primary);
}

.action-btn:hover {
  transform: translateY(-2px);
}

.swap-btn {
  background: var(--gradient-primary);
  color: white;
  border: none;
}

.swap-btn:hover {
  box-shadow: 0 4px 15px rgba(37, 99, 235, 0.4);
}

.like-container {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 15px;
}

.like-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition);
  background: var(--button-secondary-bg);
  border: 1px solid var(--card-border);
  color: var(--text-secondary);
  font-size: 1.1rem;
}

.like-btn:hover, .like-btn.liked {
  background: rgba(239, 68, 68, 0.15);
  border-color: var(--accent-red);
  color: var(--accent-red);
}

.like-btn.liked i {
  animation: pulse 0.5s;
}

.like-count {
  font-size: 0.95rem;
  color: var(--text-secondary);
}

.like-btn.liked + .like-count {
  color: var(--accent-red);
}

.copy-address-container {
  display: flex;
  gap: 10px;
  margin-top: 10px;
  flex-wrap: wrap;
}

.copy-address {
  padding: 8px 12px;
  background: var(--button-secondary-bg);
  color: var(--text-secondary);
  border-radius: 8px;
  font-size: 0.85rem;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: var(--transition);
  border: 1px solid var(--card-border);
}

.copy-address:hover {
  background: rgba(37, 99, 235, 0.2);
  border-color: var(--accent-blue);
  color: var(--accent-blue);
}

.copy-notification {
  position: fixed;
  bottom: 80px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--gradient-primary);
  color: white;
  padding: 12px 24px;
  border-radius: 30px;
  font-size: 0.9rem;
  font-weight: 500;
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  box-shadow: 0 4px 15px rgba(37, 99, 235, 0.4);
}

.copy-notification.show {
  opacity: 1;
}

/* Responsive design */
@media (max-width: 1200px) {
  .token-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  }
}

@media (max-width: 992px) {
  .header-actions .btn-text {
    display: none;
  }
  
  .header-actions .btn i {
    margin: 0;
  }
}

@media (max-width: 768px) {
  .token-grid-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }
  
  .filter-buttons {
    width: 100%;
    overflow-x: auto;
    padding-bottom: 10px;
  }
  
  .token-header-main {
    gap: 12px;
  }
  
  .token-logo {
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
  
  .token-name {
    font-size: 1rem;
  }
  
  .token-symbol {
    font-size: 0.9rem;
  }
}

@media (max-width: 576px) {
  .container {
    padding-top: 100px; /* Ensure content starts below header on mobile */
    padding-bottom: 80px; /* 2x footer height for proper spacing on mobile */
  }
  
  .trending-token {
    min-width: 250px;
  }
  
  .token-grid {
    grid-template-columns: 1fr;
  }



}