/* Manage Token Page Styles */
:root {
  --background-dark: #0a0c17;
  --card-dark: #121828;
  --card-border: #1d2a4a;
  --accent-blue: #2563eb;
  --accent-purple: #7c3aed;
  --accent-green: #10b981;
  --accent-red: #ef4444;
  --accent-orange: #f97316;
  --accent-yellow: #f59e0b;
  --text-primary: #f1f5f9;
  --text-secondary: #94a3b8;
  --glow: 0 0 15px rgba(59, 130, 246, 0.4);
  --transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  --border-radius: 12px;
}

.light-theme {
  --background-dark: #f0f4f9;
  --card-dark: #ffffff;
  --card-border: #d0d8e8;
  --accent-blue: #0089ec;
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --glow: 0 0 15px rgba(0, 137, 236, 0.2);
}

/* Manage Token Page Container */
.manage-token-page {
  min-height: 100vh;
  background-color: var(--background-dark);
  color: var(--text-primary);
  background-image: radial-gradient(circle at 10% 20%, rgba(37, 99, 235, 0.05) 0%, transparent 20%),
                    radial-gradient(circle at 90% 80%, rgba(124, 58, 237, 0.05) 0%, transparent 20%);
}

.manage-token-page .container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  padding-top: 100px; /* Ensure content starts below header */
  padding-bottom: 80px; /* 2x footer height for proper spacing */
}

/* Token Header */
.token-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 30px;
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  padding: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}

.token-logo {
  width: 60px;
  height: 60px;
  border-radius: 16px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 1.5rem;
  background-size: cover;
  background-position: center;
}

.token-info {
  flex: 1;
}

.token-name {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 4px;
  color: var(--text-primary);
}

.token-symbol {
  color: var(--text-secondary);
  font-size: 1.1rem;
  font-weight: 500;
}

/* Form Styles */
.form-container {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  margin-bottom: 40px;
  position: relative;
}

.form-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
}

.form-section {
  padding: 30px;
  border-bottom: 1px solid var(--card-border);
}

.form-section:last-child {
  border-bottom: none;
}

.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.section-title {
  font-size: 1.4rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--text-primary);
}

.section-title i {
  color: var(--accent-blue);
}

.tooltip {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.tooltip .tooltip-text {
  visibility: hidden;
  width: 250px;
  background: var(--card-dark);
  color: var(--text-primary);
  border: 1px solid var(--card-border);
  text-align: center;
  border-radius: 8px;
  padding: 10px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity 0.3s;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.tooltip .tooltip-text::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: var(--card-border) transparent transparent transparent;
}

.tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

.form-group {
  margin-bottom: 25px;
}

.form-label {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-weight: 500;
  gap: 8px;
  color: var(--text-primary);
}

.form-input {
  width: 100%;
  padding: 14px 18px;
  background: rgba(30, 41, 59, 0.3);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  color: var(--text-primary);
  font-size: 1rem;
  transition: var(--transition);
}

.form-input:focus {
  outline: none;
  border-color: var(--accent-blue);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.2);
}

.form-textarea {
  min-height: 150px;
  resize: vertical;
}

.input-group {
  display: flex;
  gap: 15px;
  margin-bottom: 15px;
}

.input-group .form-group {
  flex: 1;
}

.action-bar {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.confirm-btn {
  padding: 12px 30px;
  border-radius: 30px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  color: white;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 8px;
}

.confirm-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: var(--glow);
}

.confirm-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.action-section {
  padding: 30px;
  background: rgba(30, 41, 59, 0.3);
  border-radius: var(--border-radius);
  margin-top: 20px;
}

.action-group {
  margin-bottom: 25px;
}

.action-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
  margin-top: 20px;
}

.transfer-btn {
  padding: 12px 30px;
  border-radius: 30px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  color: white;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 8px;
}

.transfer-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: var(--glow);
}

.transfer-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.renounce-btn {
  padding: 12px 30px;
  border-radius: 30px;
  background: rgba(249, 115, 22, 0.2);
  color: var(--accent-orange);
  border: 1px solid var(--accent-orange);
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 8px;
}

.renounce-btn:hover:not(:disabled) {
  background: rgba(249, 115, 22, 0.3);
  transform: translateY(-2px);
}

.renounce-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.claim-btn {
  padding: 12px 30px;
  border-radius: 30px;
  background: linear-gradient(to right, var(--accent-green), #00a651);
  color: white;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 8px;
}

.claim-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 0 15px rgba(16, 185, 129, 0.4);
}

.claim-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.open-trading-btn {
  padding: 12px 30px;
  border-radius: 30px;
  background: linear-gradient(to right, var(--accent-yellow), #eab308);
  color: white;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
  justify-content: center;
}

.open-trading-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 0 15px rgba(245, 158, 11, 0.4);
}

.open-trading-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* Status Indicator */
.status-indicator {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 6px 12px;
  border-radius: 30px;
  font-size: 0.9rem;
  font-weight: 500;
}

.status-connected {
  background: rgba(16, 185, 129, 0.2);
  color: var(--accent-green);
}

.status-pending {
  background: rgba(59, 130, 246, 0.2);
  color: var(--accent-blue);
}

.status-warning {
  background: rgba(245, 158, 11, 0.2);
  color: var(--accent-yellow);
}

.balance-info {
  background: rgba(30, 41, 59, 0.5);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  padding: 15px;
  margin-bottom: 20px;
}

.balance-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.balance-label {
  color: var(--text-secondary);
}

.balance-value {
  font-weight: 600;
  color: var(--text-primary);
}

.max-btn {
  background: rgba(37, 99, 235, 0.2);
  color: var(--accent-blue);
  border: 1px solid var(--accent-blue);
  border-radius: 6px;
  padding: 6px 12px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: var(--transition);
}

.max-btn:hover {
  background: rgba(37, 99, 235, 0.3);
}

.logo-preview {
  width: 80px;
  height: 80px;
  border-radius: 16px;
  background-size: cover;
  background-position: center;
  margin: 15px auto;
  border: 2px dashed var(--card-border);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
}

.logo-preview i {
  font-size: 2rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .input-group {
    flex-direction: column;
    gap: 20px;
  }
  
  .action-bar {
    justify-content: center;
  }
  
  .action-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .token-header {
    flex-direction: column;
    text-align: center;
  }
  
  .form-section {
    padding: 20px;
  }
  
  .action-section {
    padding: 20px;
  }
}

@media (max-width: 576px) {
  .manage-token-page .container {
    padding: 15px;
    padding-top: 100px; /* Ensure content starts below header on mobile */
    padding-bottom: 80px; /* 2x footer height for proper spacing on mobile */
  }
  
  .section-title {
    font-size: 1.2rem;
  }
  
  .token-name {
    font-size: 1.3rem;
  }
  
  .confirm-btn,
  .transfer-btn,
  .renounce-btn,
  .claim-btn,
  .open-trading-btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}

/* Mobile Tooltip */
@media (max-width: 768px) {
  .tooltip .tooltip-text {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    max-width: 300px;
    z-index: 1000;
  }
  
  .tooltip:active .tooltip-text {
    visibility: visible;
    opacity: 1;
  }
} 