/* Token Creation Page Styles */
:root {
  --background-dark: #0a0c17;
  --card-dark: #121828;
  --card-border: #1d2a4a;
  --accent-blue: #2563eb;
  --accent-purple: #7c3aed;
  --accent-green: #10b981;
  --text-primary: #f1f5f9;
  --text-secondary: #94a3b8;
  --glow: 0 0 15px rgba(59, 130, 246, 0.4);
  --transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  --border-radius: 12px;
}

.light-theme {
  --background-dark: #f0f4f9;
  --card-dark: #ffffff;
  --card-border: #d0d8e8;
  --accent-blue: #0089ec;
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --glow: 0 0 15px rgba(0, 137, 236, 0.2);
}

/* Token Page Container */
.create-token-page .container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  padding-top: 100px; /* Ensure content starts below header */
  padding-bottom: 80px; /* 2x footer height for proper spacing */
}

/* Ensure token page styles don't affect other pages */
.create-token-page {
  min-height: 100vh;
  background-color: var(--background-dark);
  color: var(--text-primary);
}

/* Form Container */
.form-container {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  margin-bottom: 40px;
  position: relative;
}

.form-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
}

.form-header {
  padding: 25px 30px;
  border-bottom: 1px solid var(--card-border);
}

.form-title {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  gap: 12px;
  color: var(--text-primary);
}

.form-subtitle {
  color: var(--text-secondary);
  font-size: 1rem;
}

.form-content {
  padding: 30px;
}

.form-section {
  margin-bottom: 30px;
}

.section-title {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--card-border);
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--text-primary);
}

.section-title i {
  color: var(--accent-blue);
}

.form-group {
  margin-bottom: 25px;
  position: relative;
}

.form-label {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-weight: 500;
  color: var(--text-primary);
}

.required {
  color: #ef4444;
  margin-left: 5px;
}

.info-icon {
  margin-left: 8px;
  color: var(--text-secondary);
  cursor: pointer;
  position: relative;
}

.info-icon:hover .tooltip {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, 0);
}

.tooltip {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, 10px);
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: 8px;
  padding: 10px 15px;
  font-size: 0.9rem;
  width: 250px;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  transition: var(--transition);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  color: var(--text-primary);
}

.tooltip::before {
  content: '';
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  border-width: 6px;
  border-style: solid;
  border-color: transparent transparent var(--card-border) transparent;
}

.form-input {
  width: 100%;
  padding: 14px 18px;
  background: rgba(30, 41, 59, 0.3);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  color: var(--text-primary);
  font-size: 1rem;
  transition: var(--transition);
}

.form-input:focus {
  outline: none;
  border-color: var(--accent-blue);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.2);
}

.form-textarea {
  min-height: 120px;
  resize: vertical;
}

.input-group {
  display: flex;
  gap: 15px;
}

.input-group .form-group {
  flex: 1;
}

.form-footer {
  padding: 30px;
  border-top: 1px solid var(--card-border);
  text-align: center;
}

.create-btn {
  padding: 16px 40px;
  border-radius: 30px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  color: white;
  border: none;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.create-btn:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(37, 99, 235, 0.4);
}

.create-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.create-btn.creating {
  background: linear-gradient(to right, var(--accent-green), #059669);
}

.create-btn i {
  font-size: 1.2rem;
}

/* Preview Card */
.preview-card {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  padding: 25px;
  margin-top: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.preview-title {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--text-primary);
}

.preview-content {
  display: flex;
  gap: 25px;
  flex-wrap: wrap;
}

.token-preview {
  flex: 1;
  min-width: 300px;
  background: rgba(30, 41, 59, 0.3);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  padding: 20px;
}

.preview-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
}

.preview-logo {
  width: 60px;
  height: 60px;
  border-radius: 14px;
  background: linear-gradient(135deg, var(--accent-blue), var(--accent-purple));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 1.5rem;
}

.preview-info {
  flex: 1;
}

.preview-name {
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 4px;
  color: var(--text-primary);
}

.preview-symbol {
  color: var(--text-secondary);
  font-size: 1.1rem;
  font-weight: 500;
}

.preview-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  margin-top: 20px;
}

.preview-stat {
  background: rgba(30, 41, 59, 0.3);
  border: 1px solid var(--card-border);
  border-radius: 10px;
  padding: 15px;
}

.preview-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 8px;
}

.preview-value {
  font-weight: 600;
  font-size: 1.1rem;
  color: var(--text-primary);
}

/* Calendar Picker Styles */
.datetime-container {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.datetime-input {
  flex: 1;
  min-width: 250px;
  position: relative;
}

.datetime-input .form-input {
  padding-right: 40px;
  cursor: pointer;
}

.datetime-input i {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--accent-blue);
  pointer-events: none;
}

.calendar-icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--accent-blue);
  cursor: pointer;
}

.date-preview {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  color: var(--text-secondary);
}

.date-preview i {
  color: var(--accent-green);
}

/* Flatpickr Customization */
.flatpickr-input {
  background: rgba(30, 41, 59, 0.3) !important;
  border: 1px solid var(--card-border) !important;
  border-radius: var(--border-radius) !important;
  color: var(--text-primary) !important;
  padding: 14px 18px !important;
  font-size: 1rem !important;
}

.flatpickr-input:focus {
  border-color: var(--accent-blue) !important;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.2) !important;
}

/* Social Icons */
.social-icons a {
  transition: var(--transition);
}

.social-icons a:hover {
  transform: translateY(-2px);
  opacity: 0.8;
}

/* Responsive Design */
@media (max-width: 768px) {
  .input-group {
    flex-direction: column;
    gap: 20px;
  }
  
  .form-content {
    padding: 20px;
  }
  
  .preview-content {
    flex-direction: column;
  }
  
  .datetime-container {
    flex-direction: column;
  }
  
  .preview-stats {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 576px) {
  .container {
    padding: 15px;
    padding-top: 100px; /* Ensure content starts below header on mobile */
    padding-bottom: 80px; /* 2x footer height for proper spacing on mobile */
  }
  
  .form-header {
    padding: 20px;
  }
  
  .form-title {
    font-size: 1.5rem;
  }
  
  .preview-card {
    padding: 20px;
  }
  
  .token-preview {
    min-width: 250px;
  }
}

/* Focus Effects */
.form-group.focused .form-input {
  border-color: var(--accent-blue);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.2);
}

/* Mobile Tooltip */
@media (max-width: 768px) {
  .tooltip {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    max-width: 300px;
    z-index: 1000;
  }
  
  .tooltip.active {
    opacity: 1;
    visibility: visible;
  }
  
  .info-icon {
    cursor: pointer;
  }
  
  .info-icon:active .tooltip {
    opacity: 1;
    visibility: visible;
  }
}
