:root {
    --background-dark: #0a0c17;
    --card-dark: #121828;
    --card-border: #1d2a4a;
    --accent-blue: #2563eb;
    --accent-purple: #7c3aed;
    --accent-teal: #0ea5e9;
    --accent-green: #10b981;
    --accent-pink: #ff7bc2;
    --accent-red: #ef4444;
    --text-primary: #f1f5f9;
    --text-secondary: #94a3b8;
    --text-tertiary: #64748b;
    --chat-bubble: #1d2a4a;
    --chat-user: #2563eb;
    --glow: 0 0 15px rgba(59, 130, 246, 0.4);
    --transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    --border-radius: 16px;
    --up-color: #10b981;
    --down-color: #ef4444;
}

.light-theme {
    --background-dark: #f0f4f9;
    --card-dark: #ffffff;
    --card-border: #d0d8e8;
    --accent-blue: #0089ec;
    --accent-pink: #ff7bc2;
    --accent-green: #00c853;
    --text-primary: #1e293b;
    --text-secondary: #64748b;
    --glow: 0 0 15px rgba(0, 137, 236, 0.2);
    --up-color: #00c853;
    --down-color: #ff3d00;
    --chat-bubble: #e6f0ff;
    --chat-user: #0089ec;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: var(--background-dark);
    color: var(--text-primary);
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
    line-height: 1.6;
    min-height: 100vh;
    transition: var(--transition);
    overflow-x: hidden;
    padding: 15px;
}

/* Ensure no conflicts with other layouts */
.desktop-layout {
    position: relative;
    z-index: 1;
    margin-top: 0; /* Ensure no extra margin */
}

.desktop-layout .app-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

/* Ensure header doesn't overlap content */
.desktop-layout .header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
}

body::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at 10% 20%, rgba(37, 99, 235, 0.1) 0%, transparent 20%),
                radial-gradient(circle at 90% 80%, rgba(124, 58, 237, 0.1) 0%, transparent 20%);
    z-index: -1;
    pointer-events: none;
}

.desktop-layout {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    padding-top: 80px; /* Account for header height */
}

.desktop-container {
    max-width: 1920px;
    margin: 0 auto;
    padding: 20px;
    padding-top: 100px; /* Ensure content starts below header */
    padding-bottom: 80px; /* 2x footer height for proper spacing */
    display: grid;
    grid-template-columns: 300px 1fr 350px;
    gap: 20px;
    flex: 1;
    height: calc(100vh - 200px); /* Account for header and footer height */
}

/* Custom Scrollbar */
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

::-webkit-scrollbar-track {
    background: rgba(30, 41, 59, 0.3);
    border-radius: 5px;
}

::-webkit-scrollbar-thumb {
    background: var(--accent-blue);
    border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
    background: var(--accent-purple);
}

/* Left Panel - Live Chat */
.chat-panel {
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    height: 100%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
}

.chat-header {
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--card-border);
    background: rgba(30, 41, 59, 0.4);
}

.chat-title {
    display: flex;
    align-items: center;
    gap: 10px;
}

.chat-title h2 {
    font-size: 1.3rem;
}

.chat-messages {
    flex: 1;
    padding: 20px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background: rgba(18, 24, 40, 0.5);
}

.message {
    display: flex;
    gap: 12px;
    max-width: 100%;
    align-self: flex-start;
}

.message.current-user {
    align-self: flex-end;
}

.user-avatar {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: 700;
    font-size: 0.9rem;
    flex-shrink: 0;
}

.message-content {
    display: flex;
    flex-direction: column;
    gap: 6px;
    max-width: 85%;
}

.message-header {
    display: flex;
    align-items: center;
    gap: 10px;
}

.username {
    font-weight: 600;
    font-size: 0.95rem;
}

.message-time {
    color: var(--text-secondary);
    font-size: 0.8rem;
}

.message-bubble {
    background: var(--chat-bubble);
    border-radius: 18px;
    padding: 12px 16px;
    border: 1px solid var(--card-border);
    position: relative;
    max-width: 100%;
}

.message.current-user .message-bubble {
    background: var(--chat-user);
    border-color: rgba(37, 99, 235, 0.3);
    color: white;
}

.message-text {
    font-size: 0.95rem;
    line-height: 1.5;
    word-wrap: break-word;
}

.reactions {
    display: flex;
    gap: 8px;
    margin-top: 8px;
}

.reaction-btn {
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 30px;
    padding: 4px 10px;
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 0.85rem;
    cursor: pointer;
    transition: var(--transition);
}

.reaction-btn:hover {
    background: rgba(37, 99, 235, 0.2);
    border-color: var(--accent-blue);
}

.reaction-btn.active {
    background: rgba(37, 99, 235, 0.3);
    border-color: var(--accent-blue);
}

.chat-input-container {
    padding: 15px 20px;
    border-top: 1px solid var(--card-border);
    background: var(--card-dark);
    display: flex;
    gap: 12px;
    align-items: center;
}

.emoji-btn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    color: var(--text-secondary);
    cursor: pointer;
    transition: var(--transition);
    flex-shrink: 0;
}

.emoji-btn:hover {
    background: var(--accent-blue);
    color: white;
}

.message-input {
    flex: 1;
    padding: 12px 18px;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 30px;
    color: var(--text-primary);
    font-size: 1rem;
    resize: none;
    height: 50px;
    max-height: 150px;
}

.message-input:focus {
    outline: none;
    border-color: var(--accent-blue);
}

.send-btn {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--accent-blue);
    color: white;
    border: none;
    cursor: pointer;
    transition: var(--transition);
    flex-shrink: 0;
}

.send-btn:hover {
    transform: scale(1.05);
    box-shadow: var(--glow);
}

.send-btn.disabled {
    background: var(--card-border);
    cursor: not-allowed;
    opacity: 0.7;
}

/* Center Panel - Transactions */
.center-panel {
    display: flex;
    flex-direction: column;
    gap: 20px;
    height: 100%;
}

.token-header {
    display: flex;
    align-items: center;
    gap: 15px;
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    padding: 20px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
}

.token-logo {
    width: 60px;
    height: 60px;
    border-radius: 16px;
    background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: 700;
    font-size: 1.5rem;
}

.token-info {
    flex: 1;
}

.token-name {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 4px;
}

.token-symbol {
    color: var(--text-secondary);
    font-size: 1.1rem;
    font-weight: 500;
}

.token-stats {
    display: flex;
    gap: 30px;
}

.token-stat {
    display: flex;
    flex-direction: column;
}

.stat-label {
    color: var(--text-secondary);
    font-size: 0.9rem;
    margin-bottom: 4px;
}

.stat-value {
    font-weight: 700;
    font-size: 1.2rem;
}

.price-change.positive {
    color: var(--accent-green);
}

.price-change.negative {
    color: var(--accent-red);
}

.chart-panel {
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    overflow: hidden;
    position: relative;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
    height: 300px; /* Fixed height instead of flex: 1 */
}

.chart-header {
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--card-border);
    background: rgba(30, 41, 59, 0.4);
}

.chart-title {
    display: flex;
    align-items: center;
    gap: 10px;
}

.chart-title h2 {
    font-size: 1.3rem;
}

.chart-controls {
    display: flex;
    gap: 10px;
}

.chart-btn {
    padding: 8px 16px;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 20px;
    color: var(--text-secondary);
    font-weight: 500;
    cursor: pointer;
    transition: var(--transition);
}

.chart-btn.active, .chart-btn:hover {
    background: var(--accent-blue);
    color: white;
    border-color: var(--accent-blue);
}

.chart-wrapper {
    height: calc(100% - 60px);
    padding: 15px;
    position: relative;
    overflow: hidden;
    background: transparent; /* Remove any background */
}

/* Ensure only one chart is visible */
.chart-wrapper > div {
    width: 100% !important;
    height: 100% !important;
}

.transactions-container {
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
    height: 600px; /* Desktop view height */
    display: flex;
    flex-direction: column;
    margin-top: 20px; /* Add space between chart and transactions */
}

.table-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 20px;
    border-bottom: 1px solid var(--card-border);
    background: rgba(30, 41, 59, 0.4);
}

.table-title {
    font-size: 1.2rem;
    font-weight: 600;
}

.table-filters {
    display: flex;
    gap: 10px;
    align-items: center;
}

.filter-btn {
    padding: 8px 16px;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 20px;
    color: var(--text-secondary);
    font-weight: 500;
    cursor: pointer;
    transition: var(--transition);
}

.filter-btn.active, .filter-btn:hover {
    background: var(--accent-blue);
    color: white;
    border-color: var(--accent-blue);
}

.history-btn {
    padding: 8px 16px;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 20px;
    color: var(--text-secondary);
    font-weight: 500;
    cursor: pointer;
    transition: var(--transition);
    display: flex;
    align-items: center;
    gap: 8px;
}

.history-btn:hover {
    background: var(--accent-purple);
    color: white;
    border-color: var(--accent-purple);
}

.transactions-table {
    width: 100%;
    border-collapse: collapse;
    flex: 1;
    overflow-y: auto;
    display: block;
    background: rgba(18, 24, 40, 0.5);
    max-height: calc(600px - 80px); /* Account for header height */
}

.transactions-table th {
    text-align: left;
    padding: 15px 20px;
    color: var(--text-secondary);
    font-weight: 500;
    border-bottom: 1px solid var(--card-border);
    background: rgba(30, 41, 59, 0.6);
    position: sticky;
    top: 0;
}

.transactions-table td {
    padding: 12px 20px;
    border-bottom: 1px solid var(--card-border);
    white-space: nowrap;
}

.transactions-table tr:last-child td {
    border-bottom: none;
}

.transactions-table tr:nth-child(even) {
    background: rgba(30, 41, 59, 0.3);
}

.type-buy {
    color: var(--accent-green);
}

.type-sell {
    color: var(--accent-red);
}

.type-icon {
    margin-right: 8px;
}

/* Right Panel - Swap */
.swap-panel {
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    padding: 20px; /* Reduced from 25px */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
    position: relative;
    height: 60%;
    display: flex;
    flex-direction: column;
}

.panel-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px; /* Reduced from 25px */
}

.panel-title {
    font-size: 1.4rem;
    font-weight: 700;
}

.settings-btn {
    background: transparent;
    border: none;
    color: var(--text-secondary);
    font-size: 1.2rem;
    cursor: pointer;
    padding: 8px;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.settings-btn:hover {
    background: rgba(37, 99, 235, 0.1);
    color: var(--accent-blue);
}

.swap-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--text-secondary);
    cursor: pointer;
    transition: var(--transition);
    margin: -10px auto;
    position: relative;
    z-index: 2;
}

.swap-icon:hover {
    background: var(--accent-blue);
    border-color: var(--accent-blue);
    color: white;
    transform: rotate(180deg);
}

.token-card {
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 14px;
    padding: 12px; /* Reduced from 20px */
    margin-bottom: 15px; /* Reduced from 20px */
    transition: var(--transition);
}

.token-card:hover {
    border-color: var(--accent-blue);
}

.token-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px; /* Reduced from 15px */
}

.token-label {
    font-size: 0.95rem;
    color: var(--text-secondary);
    font-weight: 500;
}

.token-balance {
    font-size: 0.9rem;
    color: var(--text-secondary);
}

.token-input-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.token-amount {
    flex: 1;
    background: transparent;
    border: none;
    color: var(--text-primary);
    font-size: 1.4rem; /* Reduced from 1.8rem */
    font-weight: 600;
    outline: none;
    max-width: 70%;
}

.token-selector {
    display: flex;
    align-items: center;
    gap: 8px; /* Reduced from 10px */
    background: rgba(37, 99, 235, 0.1);
    padding: 6px 12px; /* Reduced from 8px 15px */
    border-radius: 30px;
    cursor: pointer;
    transition: var(--transition);
}

.token-selector:hover {
    background: rgba(37, 99, 235, 0.2);
}

.token-logo-small {
    width: 24px; /* Reduced from 28px */
    height: 24px; /* Reduced from 28px */
    border-radius: 50%;
    background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: 700;
    font-size: 0.8rem; /* Reduced from 0.9rem */
}

.token-name {
    font-weight: 600;
    font-size: 1rem; /* Reduced from 1.1rem */
}

.token-dropdown {
    font-size: 1rem;
    color: var(--text-secondary);
}

.price-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0; /* Reduced from 15px */
    font-size: 0.9rem;
    color: var(--text-secondary);
    border-bottom: 1px solid var(--card-border);
    margin-bottom: 15px; /* Reduced from 20px */
}

.swap-btn {
    width: 100%;
    padding: 14px; /* Reduced from 18px */
    border-radius: 16px;
    background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
    color: white;
    border: none;
    font-size: 1rem; /* Reduced from 1.1rem */
    font-weight: 600;
    cursor: pointer;
    transition: var(--transition);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-top: auto;
}

.swap-btn:hover {
    transform: translateY(-2px);
    box-shadow: var(--glow);
}

.swap-btn.disabled {
    background: var(--card-border);
    cursor: not-allowed;
    opacity: 0.7;
}

/* Token Selector Modal */
.token-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(10, 12, 23, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 1;
    pointer-events: all;
    transition: opacity 0.3s ease;
    backdrop-filter: blur(5px);
}

.modal-content {
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    width: 90%;
    max-width: 450px;
    max-height: 80vh;
    overflow: hidden;
    transform: translateY(0);
    transition: transform 0.3s ease;
}

.modal-header {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--card-border);
}

.modal-title {
    font-size: 1.3rem;
    font-weight: 600;
}

.close-modal {
    background: transparent;
    border: none;
    color: var(--text-secondary);
    font-size: 1.5rem;
    cursor: pointer;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: var(--transition);
}

.close-modal:hover {
    background: rgba(239, 68, 68, 0.1);
    color: var(--accent-red);
}

.modal-search {
    padding: 15px 20px;
    border-bottom: 1px solid var(--card-border);
}

.search-input {
    width: 100%;
    padding: 12px 20px;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 30px;
    color: var(--text-primary);
    font-size: 1rem;
}

.search-input:focus {
    outline: none;
    border-color: var(--accent-blue);
}

.token-list {
    max-height: 50vh;
    overflow-y: auto;
}

.token-item {
    display: flex;
    align-items: center;
    gap: 15px;
    padding: 15px 20px;
    cursor: pointer;
    transition: var(--transition);
    border-bottom: 1px solid var(--card-border);
}

.token-item:last-child {
    border-bottom: none;
}

.token-item:hover {
    background: rgba(37, 99, 235, 0.1);
}

.token-info {
    flex: 1;
}

.token-item-name {
    font-weight: 600;
    margin-bottom: 4px;
}

.token-item-symbol {
    color: var(--text-secondary);
    font-size: 0.9rem;
}

.token-item-balance {
    font-size: 0.9rem;
    color: var(--text-secondary);
}

/* Responsive */
@media (max-width: 1400px) {
    .desktop-container {
        grid-template-columns: 280px 1fr 300px;
    }
}

/* Ensure footer is at bottom for desktop layout */
.desktop-layout .footer {
    margin-top: auto;
    position: relative;
    bottom: 0;
    width: 100%;
}

@media (max-width: 1200px) {
    .desktop-container {
        grid-template-columns: 250px 1fr 280px;
    }
}

/* Light mode overrides */
body.light-theme {
    --background-dark: #f0f4f9;
    --card-dark: #ffffff;
    --card-border: #d0d8e8;
    --accent-blue: #0089ec;
    --accent-pink: #ff7bc2;
    --accent-green: #00c853;
    --text-primary: #1e293b;
    --text-secondary: #64748b;
    --glow: 0 0 15px rgba(0, 137, 236, 0.2);
    --up-color: #00c853;
    --down-color: #ff3d00;
    --chat-bubble: #e6f0ff;
    --chat-user: #0089ec;
}

body.light-theme .chat-panel,
body.light-theme .center-panel .token-header,
body.light-theme .chart-panel,
body.light-theme .transactions-container,
body.light-theme .swap-panel,
body.light-theme .token-modal .modal-content {
    background: var(--card-dark) !important;
    border-color: var(--card-border) !important;
    color: var(--text-primary) !important;
}

body.light-theme .token-logo,
body.light-theme .token-logo-small,
body.light-theme .user-avatar {
    background: linear-gradient(to right, var(--accent-blue), var(--accent-pink)) !important;
}

body.light-theme .swap-btn {
    background: linear-gradient(to right, var(--accent-blue), var(--accent-pink));
}

body.light-theme .message-bubble {
    background: var(--chat-bubble) !important;
    color: var(--text-primary) !important;
}

body.light-theme .message.current-user .message-bubble {
    background: var(--chat-user) !important;
    color: #fff !important;
}

body.light-theme .reaction-btn {
    background: #e6f0ff;
    border-color: var(--card-border);
}

body.light-theme .reaction-btn.active {
    background: #cce4ff;
    border-color: var(--accent-blue);
}

body.light-theme .filter-btn.active, 
body.light-theme .filter-btn:hover {
    background: var(--accent-blue);
    color: #fff;
    border-color: var(--accent-blue);
}

body.light-theme .swap-icon:hover {
    background: var(--accent-blue);
    color: #fff;
}

body.light-theme .token-selector:hover {
    background: #e6f0ff;
}

body.light-theme .token-card:hover {
    border-color: var(--accent-blue);
}

body.light-theme .search-input {
    background: #f8fafc;
    border-color: #cbd5e1;
    color: #1e293b;
}

/* Holders Container */
.holders-container {
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    overflow: hidden;
    padding: 20px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.holders-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.holders-title {
    font-size: 1.2rem;
    font-weight: 600;
    color: var(--text-primary);
}

.view-all {
    color: var(--accent-blue);
    text-decoration: none;
    font-weight: 500;
    transition: var(--transition);
}

.view-all:hover {
    text-decoration: underline;
    color: var(--accent-purple);
}

.holders-scroll {
    display: flex;
    flex-direction: column;
    gap: 12px;
    flex: 1;
    overflow-y: auto;
    padding-right: 5px;
}

.holder-card {
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 10px;
    padding: 12px 16px;
    display: flex;
    align-items: center;
    gap: 12px;
    min-height: 50px;
    transition: var(--transition);
    box-shadow: none;
}

.holder-card:hover {
    border-color: var(--accent-blue);
    background: rgba(30, 41, 59, 0.8);
}

.holder-address {
    font-weight: 600;
    font-size: 0.95rem;
    flex: 1 1 40%;
    margin-bottom: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: var(--text-primary);
}

.holder-amount {
    color: var(--text-secondary);
    font-size: 0.95rem;
    flex: 1 1 35%;
    text-align: right;
    font-weight: 500;
}

.holder-percent {
    font-weight: 700;
    font-size: 0.95rem;
    flex: 0 0 15%;
    text-align: right;
    color: var(--accent-green);
}

.explorer-btn {
    flex: 0 0 24px;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    color: var(--accent-blue);
    cursor: pointer;
    margin-left: 8px;
    font-size: 1rem;
    transition: color 0.2s;
    text-decoration: none;
}

.explorer-btn:hover {
    color: var(--accent-purple);
}

/* Light theme adjustments for holders */
body.light-theme .holders-container {
    background: var(--card-dark);
    border-color: var(--card-border);
}

body.light-theme .holder-card {
    background: rgba(255, 255, 255, 0.8);
    border-color: var(--card-border);
}

body.light-theme .holder-card:hover {
    background: rgba(255, 255, 255, 0.9);
    border-color: var(--accent-blue);
}

body.light-theme .holder-address {
    color: var(--text-primary);
}

body.light-theme .holder-amount {
    color: var(--text-secondary);
}

body.light-theme .holder-percent {
    color: var(--accent-green);
}

body.light-theme .explorer-btn {
    color: var(--accent-blue);
}

body.light-theme .explorer-btn:hover {
    color: var(--accent-purple);
}
