/* Wallet Page Styles */
:root {
  --background-dark: #0a0c17;
  --card-dark: #121828;
  --card-border: #1d2a4a;
  --accent-blue: #2563eb;
  --accent-purple: #7c3aed;
  --accent-teal: #0ea5e9;
  --accent-green: #10b981;
  --accent-pink: #ff7bc2;
  --accent-red: #ef4444;
  --text-primary: #f1f5f9;
  --text-secondary: #94a3b8;
  --text-tertiary: #64748b;
  --glow: 0 0 15px rgba(59, 130, 246, 0.4);
  --transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  --border-radius: 16px;
  --up-color: #10b981;
  --down-color: #ef4444;
  --radial-gradient: radial-gradient(circle at 10% 20%, rgba(37, 99, 235, 0.1) 0%, transparent 20%),
                    radial-gradient(circle at 90% 80%, rgba(124, 58, 237, 0.1) 0%, transparent 20%);
}

.light-theme {
  --background-dark: #f0f4f9;
  --card-dark: #ffffff;
  --card-border: #d0d8e8;
  --accent-blue: #0089ec;
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --glow: 0 0 15px rgba(0, 137, 236, 0.2);
}

/* Wallet Page Container */
.wallet-page {
  min-height: 100vh;
  background-color: var(--background-dark);
  color: var(--text-primary);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.6;
  transition: var(--transition);
  overflow-x: hidden;
  padding: 15px;
}

.wallet-page::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--radial-gradient);
  z-index: -1;
  pointer-events: none;
}

.wallet-page .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  padding-top: 100px; /* Ensure content starts below header */
  padding-bottom: 80px; /* 2x footer height for proper spacing */
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Custom Scrollbar */
.wallet-page ::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

.wallet-page ::-webkit-scrollbar-track {
  background: rgba(30, 41, 59, 0.3);
  border-radius: 5px;
}

.wallet-page ::-webkit-scrollbar-thumb {
  background: var(--accent-blue);
  border-radius: 5px;
}

.wallet-page ::-webkit-scrollbar-thumb:hover {
  background: var(--accent-purple);
}

/* Wallet Section */
.wallet-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.section-title {
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 10px;
}

.wallet-summary {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  padding: 25px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
}

.total-balance {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.balance-label {
  color: var(--text-secondary);
  font-size: 1.1rem;
  margin-bottom: 8px;
}

.balance-value {
  font-size: 2.8rem;
  font-weight: 700;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.wallet-address {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  border-radius: 30px;
  padding: 12px 20px;
  width: fit-content;
  margin: 0 auto;
  transition: var(--transition);
}

.wallet-address:hover {
  border-color: var(--accent-blue);
}

.address-text {
  font-family: monospace;
  font-size: 1.1rem;
  letter-spacing: 0.5px;
  color: var(--text-primary);
}

.copy-btn {
  background: transparent;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  transition: var(--transition);
  padding: 5px;
}

.copy-btn:hover:not(:disabled) {
  color: var(--accent-blue);
}

.copy-btn:disabled {
  color: var(--accent-green);
}

.network-info {
  display: flex;
  align-items: center;
  gap: 10px;
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  border-radius: 30px;
  padding: 8px 16px;
  width: fit-content;
  margin: 0 auto;
}

.network-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: var(--accent-green);
}

.network-name {
  font-weight: 500;
  color: var(--text-primary);
}

.disconnect-btn {
  width: 100%;
  padding: 14px;
  border-radius: 16px;
  background: linear-gradient(to right, var(--accent-red), #f97583);
  color: white;
  border: none;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
}

.disconnect-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 0 15px rgba(239, 68, 68, 0.4);
}

.disconnect-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.tokens-container {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
}

.tokens-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 1px solid var(--card-border);
  background: rgba(30, 41, 59, 0.4);
}

.tokens-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
}

.tokens-list {
  display: flex;
  flex-direction: column;
}

.token-header {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  padding: 0 20px;
  color: var(--text-secondary);
  font-weight: 500;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--card-border);
}

.header-item {
  display: flex;
  align-items: center;
  gap: 8px;
}

.token-item {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  padding: 16px 20px;
  border-bottom: 1px solid var(--card-border);
  transition: var(--transition);
  cursor: pointer;
}

.token-item:last-child {
  border-bottom: none;
}

.token-item:hover {
  background: rgba(30, 41, 59, 0.3);
}

.token-info {
  display: flex;
  align-items: center;
  gap: 15px;
}

.token-logo {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  overflow: hidden;
}

.token-logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.token-details {
  display: flex;
  flex-direction: column;
}

.token-name {
  font-weight: 600;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-primary);
}

.token-symbol {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.token-balance {
  text-align: right;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.balance-amount {
  font-weight: 600;
  color: var(--text-primary);
}

.balance-usd {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.token-value {
  text-align: right;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.value-amount {
  font-weight: 700;
  color: var(--text-primary);
}

.value-change {
  display: inline-block;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 600;
  margin-top: 4px;
}

.value-change.positive {
  background: rgba(16, 185, 129, 0.2);
  color: var(--accent-green);
}

.value-change.negative {
  background: rgba(239, 68, 68, 0.2);
  color: var(--accent-red);
}

.token-actions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
}

.action-btn {
  padding: 8px 16px;
  border-radius: 10px;
  font-weight: 500;
  font-size: 0.9rem;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 6px;
  border: none;
}

.view-btn {
  background: rgba(37, 99, 235, 0.1);
  color: var(--accent-blue);
  border: 1px solid var(--card-border);
}

.view-btn:hover {
  background: rgba(37, 99, 235, 0.2);
  border-color: var(--accent-blue);
}

.highlight {
  animation: highlight 2s ease;
}

@keyframes highlight {
  0% { background-color: rgba(37, 99, 235, 0.2); }
  100% { background-color: transparent; }
}

.transactions-section {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
  height: 300px;
  display: flex;
  flex-direction: column;
}

.transactions-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 1px solid var(--card-border);
  background: rgba(30, 41, 59, 0.4);
}

.transactions-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
}

.view-all-btn {
  color: var(--accent-blue);
  background: transparent;
  border: none;
  cursor: pointer;
  font-weight: 500;
  transition: var(--transition);
}

.view-all-btn:hover {
  text-decoration: underline;
}

.transactions-list {
  flex: 1;
  overflow-y: auto;
}

.transaction-item {
  padding: 15px 20px;
  border-bottom: 1px solid var(--card-border);
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: var(--transition);
}

.transaction-item:last-child {
  border-bottom: none;
}

.transaction-item:hover {
  background: rgba(30, 41, 59, 0.3);
}

.transaction-info {
  display: flex;
  align-items: center;
  gap: 15px;
}

.transaction-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(37, 99, 235, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--accent-blue);
}

.transaction-details {
  display: flex;
  flex-direction: column;
}

.transaction-name {
  font-weight: 600;
  margin-bottom: 4px;
  color: var(--text-primary);
}

.transaction-time {
  color: var(--text-secondary);
  font-size: 0.85rem;
}

.transaction-value {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.value-amount {
  font-weight: 700;
  margin-bottom: 4px;
  color: var(--text-primary);
}

.value-status {
  display: inline-block;
  padding: 4px 10px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
}

.value-status.completed {
  background: rgba(16, 185, 129, 0.2);
  color: var(--accent-green);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .token-header, .token-item {
    grid-template-columns: 2fr 1fr 1fr;
  }
  
  .tokens-list {
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .token-header, .token-item {
    grid-template-columns: 2fr 1fr;
  }
  
  .token-balance, .token-value {
    text-align: left;
  }
  
  .balance-usd {
    display: none;
  }
  
  .wallet-page .container {
    padding: 15px;
    padding-top: 100px; /* Ensure content starts below header on mobile */
  }
  
  .balance-value {
    font-size: 2.2rem;
  }
}

@media (max-width: 576px) {
  .token-header, .token-item {
    grid-template-columns: 1fr 1fr;
  }
  
  .token-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .token-actions {
    width: 100%;
    justify-content: flex-end;
  }
  
  .balance-amount {
    font-size: 1.1rem;
  }
  
  .wallet-page {
    padding: 10px;
  }
  
  .wallet-summary {
    padding: 20px;
  }
  
  .wallet-page .container {
    padding-top: 100px; /* Ensure content starts below header on mobile */
    padding-bottom: 80px; /* 2x footer height for proper spacing on mobile */
  }
} 