@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&family=Orbitron:wght@400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--background);
  color: var(--text-primary);
  font-family: 'Inter', sans-serif;
  line-height: 1.6;
  overflow-x: hidden;
  min-height: 100vh;
  padding-top: 80px;
  padding-bottom: 140px; /* ensure content is not hidden behind fixed footer (2x footer height) */
  position: relative;
  transition: background-color 0.3s ease, color 0.3s ease;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--radial-gradient);
  z-index: -1;
  pointer-events: none;
  transition: background 0.5s ease;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  font-family: 'Inter', sans-serif;
  cursor: pointer;
}

/* Animations */
@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

@keyframes glow {
  0% { box-shadow: 0 0 5px var(--accent-blue); }
  50% { box-shadow: 0 0 20px var(--accent-blue); }
  100% { box-shadow: 0 0 5px var(--accent-blue); }
}





.theme-toggle {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--button-secondary-bg);
  border: 1px solid var(--card-border);
  color: var(--text-primary);
  cursor: pointer;
  transition: var(--transition);
}

.theme-toggle:hover {
  transform: rotate(15deg);
  background: var(--button-secondary-hover);
  border-color: var(--accent-purple);
}







/* Add to existing global.css */

/* Header Styles */
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--header-bg);
  backdrop-filter: blur(10px);
  z-index: 100;
  border-bottom: 1px solid var(--card-border);
  padding: 15px 5%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.logo {
  display: flex;
  align-items: center;
  gap: 12px;
  font-family: 'Orbitron', sans-serif;
  font-weight: 700;
  font-size: 1.8rem;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: var(--glow);
}

.logo-icon {
  font-size: 1.5rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.search-container {
  flex: 1;
  max-width: 500px;
  margin: 0 30px;
  position: relative;
}

.search-container input {
  width: 100%;
  padding: 12px 20px 12px 50px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid var(--card-border);
  border-radius: 30px;
  color: var(--text-primary);
  font-size: 1rem;
  transition: var(--transition);
  background-color: var(--button-secondary-bg);
}

.search-container input:focus {
  outline: none;
  border-color: var(--accent-blue);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.3);
}

.search-icon {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--text-secondary);
}

.header-actions {
  display: flex;
  gap: 15px;
  align-items: center;
}

.btn {
  padding: 12px 24px;
  border-radius: 30px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 8px;
  border: none;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
  box-shadow: 0 4px 15px rgba(37, 99, 235, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(37, 99, 235, 0.4);
}

.btn-secondary {
  background: var(--button-secondary-bg);
  color: var(--text-primary);
  border: 1px solid var(--card-border);
}

.btn-secondary:hover {
  background: var(--button-secondary-hover);
  border-color: var(--accent-blue);
}

.theme-toggle {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--button-secondary-bg);
  border: 1px solid var(--card-border);
  color: var(--text-primary);
  cursor: pointer;
  transition: var(--transition);
}

.theme-toggle:hover {
  transform: rotate(15deg);
  background: var(--button-secondary-hover);
  border-color: var(--accent-purple);
}

/* Footer Styles */
footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: var(--footer-bg);
  backdrop-filter: blur(10px);
  border-top: 1px solid var(--card-border);
  z-index: 100;
  padding: 15px 0;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.footer-nav {
  display: flex;
  justify-content: space-around;
  max-width: 800px;
  margin: 0 auto;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  cursor: pointer;
  transition: var(--transition);
  padding: 8px 15px;
  border-radius: 12px;
}

.nav-item:hover, .nav-item.active {
  background: rgba(37, 99, 235, 0.15);
}

.nav-icon {
  font-size: 1.3rem;
  color: var(--text-secondary);
}

.nav-item.active .nav-icon {
  color: var(--accent-blue);
}

.nav-label {
  font-size: 0.8rem;
  color: var(--text-secondary);
}

.nav-item.active .nav-label {
  color: var(--accent-blue);
}

/* Responsive Header and Footer */
@media (max-width: 992px) {
  header {
    padding: 15px 20px;
  }
  
  .search-container {
    margin: 0 15px;
    max-width: 400px;
  }
  
  .btn {
    padding: 10px 18px;
    font-size: 0.9rem;
  }
}

@media (max-width: 768px) {
  .header-actions {
    gap: 10px;
  }
  
  .btn .btn-text {
    display: none;
  }
  
  .btn i {
    margin: 0;
  }
}

@media (max-width: 576px) {
  body {
    padding-top: 70px;
    padding-bottom: 130px; /* ensure content is not hidden behind fixed footer on mobile (2x footer height) */
  }
  
  header {
    padding: 12px 15px;
  }
  
  .logo {
    font-size: 1.5rem;
  }
  
  .search-container {
    margin: 0 10px;
  }
  
  .search-container input {
    padding: 10px 15px 10px 40px;
  }
  
  .search-icon {
    left: 15px;
  }
  
  .footer-nav {
    padding: 0 10px;
  }
  
  .nav-item {
    padding: 8px 10px;
  }
}

@media (max-width: 900px) {
  body {
    padding-bottom: 140px; /* ensure content is not hidden behind fixed footer (2x footer height) */
  }
  footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding-bottom: 0;
  }
}

/* Hide scrollbar but keep scrolling functional */
body, * {
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;
}
body::-webkit-scrollbar, *::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}
body::-webkit-scrollbar-thumb, *::-webkit-scrollbar-thumb {
  background: transparent;
}