:root {
    --background-dark: #0a0c17;
    --card-dark: #121828;
    --card-border: #1d2a4a;
    --accent-blue: #2563eb;
    --accent-purple: #7c3aed;
    --accent-teal: #0ea5e9;
    --accent-green: #10b981;
    --accent-pink: #ff7bc2;
    --text-primary: #f1f5f9;
    --text-secondary: #94a3b8;
    --glow: 0 0 15px rgba(59, 130, 246, 0.4);
    --transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    --border-radius: 16px;
    --up-color: #10b981;
    --down-color: #ef4444;
}

.swap-page-v2 .container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 30px 20px 80px; /* 2x footer height for proper spacing */
  padding-top: 100px; /* Ensure content starts below header */
}

/* Mobile optimized styles */
.swap-page-v2.mobile-optimized .container {
  max-width: 100%;
  padding: 15px 10px 80px; /* 2x footer height for proper spacing */
  padding-top: 100px; /* Ensure content starts below header */
}

.mobile-notice {
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  color: white;
  padding: 10px 15px;
  border-radius: 10px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 0.9rem;
  font-weight: 500;
  box-shadow: 0 2px 10px rgba(37, 99, 235, 0.3);
}

.mobile-notice i {
  font-size: 1.1rem;
}



.chart-container {
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    overflow: hidden;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    height: 500px;
    position: relative;
    margin-bottom: 30px;
}

/* Chart Panel */
.chart-panel {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  min-height: 350px;
  margin-bottom: 30px;
}

.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 1px solid var(--card-border);
}

.chart-title {
  display: flex;
  align-items: center;
  gap: 10px;
}

.chart-title h2 {
  font-size: 1.3rem;
  margin: 0;
}

.chart-controls {
  display: flex;
  gap: 10px;
}

.chart-actions {
  display: flex;
  gap: 10px;
}

.chart-wrapper {
  height: 350px;
}

.chart-btn {
  padding: 8px 16px;
  background: transparent;
  border: 1px solid var(--card-border);
  border-radius: 20px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: var(--transition);
  font-size: 0.9rem;
  font-weight: 500;
}

.chart-btn:hover {
  background: rgba(37, 99, 235, 0.1);
  border-color: var(--accent-blue);
  color: var(--accent-blue);
}

.chart-btn.active {
  background: var(--accent-blue);
  color: white;
  border-color: var(--accent-blue);
}

/* Swap Panel */
.swap-panel {
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    padding: 25px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    position: relative;
  margin-bottom: 30px;
}

.panel-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;
}

.panel-title {
    font-size: 1.4rem;
    font-weight: 700;
}

.settings-btn {
    background: transparent;
    border: none;
    color: var(--text-secondary);
    font-size: 1.2rem;
    cursor: pointer;
    padding: 8px;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: var(--transition);
}
.settings-btn:hover {
    background: rgba(37, 99, 235, 0.1);
    color: var(--accent-blue);
}

.swap-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--text-secondary);
    cursor: pointer;
    transition: var(--transition);
    margin: -10px auto 20px auto;
    position: relative;
    z-index: 2;
}
.swap-icon:hover {
    background: var(--accent-blue);
    border-color: var(--accent-blue);
    color: white;
    transform: rotate(180deg);
}

.token-card {
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 14px;
    padding: 20px;
    margin-bottom: 20px;
    transition: var(--transition);
}
.token-card:hover {
    border-color: var(--accent-blue);
}
.token-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}
.token-label {
    font-size: 0.95rem;
    color: var(--text-secondary);
    font-weight: 500;
}
.token-balance {
    font-size: 0.9rem;
    color: var(--text-secondary);
}
.token-input-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.token-amount {
    flex: 1;
    background: transparent;
    border: none;
    color: var(--text-primary);
    font-size: 1.8rem;
    font-weight: 600;
    outline: none;
    max-width: 70%;
}
.token-selector {
    display: flex;
    align-items: center;
    gap: 10px;
    background: rgba(37, 99, 235, 0.1);
    padding: 8px 15px;
    border-radius: 30px;
    cursor: pointer;
    transition: var(--transition);
}
.token-selector:hover {
    background: rgba(37, 99, 235, 0.2);
}
.token-logo {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: 700;
    font-size: 0.9rem;
}
.token-name {
    font-weight: 600;
    font-size: 1.1rem;
}
.token-dropdown {
    font-size: 1rem;
    color: var(--text-secondary);
}
.price-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    font-size: 0.9rem;
    color: var(--text-secondary);
    border-bottom: 1px solid var(--card-border);
    margin-bottom: 20px;
}
.swap-btn {
    width: 100%;
    padding: 18px;
    border-radius: 16px;
    background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
    color: white;
    border: none;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    transition: var(--transition);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}
.swap-btn:hover {
    transform: translateY(-2px);
    box-shadow: var(--glow);
}
.swap-btn.disabled {
    background: var(--card-border);
    cursor: not-allowed;
    opacity: 0.7;
}
.swap-btn.disabled:hover {
    transform: none;
    box-shadow: none;
}

/* Slippage Settings Modal */
.slippage-modal {
    position: absolute;
    top: 60px;
    right: 25px;
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    padding: 20px;
    width: 300px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
    z-index: 100;
    opacity: 1;
    pointer-events: all;
    transform: translateY(0);
    transition: var(--transition);
}
.slippage-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}
.slippage-title {
    font-size: 1.1rem;
    font-weight: 600;
}
.close-slippage {
    background: transparent;
    border: none;
    color: var(--text-secondary);
    cursor: pointer;
    font-size: 1.2rem;
}
.slippage-options {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin-bottom: 15px;
}
.slippage-option {
    padding: 8px;
    text-align: center;
    border: 1px solid var(--card-border);
    border-radius: 8px;
    cursor: pointer;
    transition: var(--transition);
}
.slippage-option:hover {
    border-color: var(--accent-blue);
}
.slippage-option.active {
    background: var(--accent-blue);
    color: white;
    border-color: var(--accent-blue);
}
.custom-slippage {
    display: flex;
    align-items: center;
    gap: 10px;
}
.custom-slippage input {
    flex: 1;
    padding: 10px;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 8px;
    color: var(--text-primary);
    text-align: center;
}
.custom-slippage span {
    color: var(--text-secondary);
}

/* Token Selector Modal */
.token-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(10, 12, 23, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 1;
    pointer-events: all;
    transition: opacity 0.3s ease;
    backdrop-filter: blur(5px);
}
.modal-content {
    background: var(--card-dark);
    border: 1px solid var(--card-border);
    border-radius: var(--border-radius);
    width: 90%;
    max-width: 450px;
    max-height: 80vh;
    overflow: hidden;
    transform: translateY(0);
    transition: transform 0.3s ease;
}
.modal-header {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--card-border);
}
.modal-title {
    font-size: 1.3rem;
    font-weight: 600;
}
.close-modal {
    background: transparent;
    border: none;
    color: var(--text-secondary);
    font-size: 1.5rem;
    cursor: pointer;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: var(--transition);
}
.close-modal:hover {
    background: rgba(239, 68, 68, 0.1);
    color: var(--accent-red);
}
.modal-search {
    padding: 15px 20px;
    border-bottom: 1px solid var(--card-border);
}
.search-input {
    width: 100%;
    padding: 12px 20px;
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid var(--card-border);
    border-radius: 30px;
    color: var(--text-primary);
    font-size: 1rem;
}
.search-input:focus {
    outline: none;
    border-color: var(--accent-blue);
}
.token-list {
    max-height: 50vh;
    overflow-y: auto;
}
.token-item {
    display: flex;
    align-items: center;
    gap: 15px;
    padding: 15px 20px;
    cursor: pointer;
    transition: var(--transition);
    border-bottom: 1px solid var(--card-border);
}
.token-item:last-child {
    border-bottom: none;
}
.token-item:hover {
    background: rgba(37, 99, 235, 0.1);
}
.token-info {
    flex: 1;
}
.token-item-name {
    font-weight: 600;
    margin-bottom: 4px;
}
.token-item-symbol {
    color: var(--text-secondary);
    font-size: 0.9rem;
}
.token-item-balance {
    font-size: 0.9rem;
    color: var(--text-secondary);
}

/* Transaction Container */
.transactions-container {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  overflow: hidden;
  margin-bottom: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}
.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 1px solid var(--card-border);
}
.table-title {
  font-size: 1.2rem;
  font-weight: 600;
}
.table-filters {
  display: flex;
  gap: 10px;
}
.filter-btn {
  padding: 8px 16px;
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  border-radius: 20px;
  color: var(--text-secondary);
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
}
.filter-btn.active, .filter-btn:hover {
  background: var(--accent-blue);
  color: white;
  border-color: var(--accent-blue);
}
.transactions-table {
  width: 100%;
  border-collapse: collapse;
}
.transactions-table th {
  text-align: left;
  padding: 15px 20px;
  color: var(--text-secondary);
  font-weight: 500;
  border-bottom: 1px solid var(--card-border);
  background: rgba(30, 41, 59, 0.6);
}
.transactions-table td {
  padding: 12px 20px;
  border-bottom: 1px solid var(--card-border);
}
.transactions-table tr:last-child td {
  border-bottom: none;
}
.transactions-table tr:nth-child(even) {
  background: rgba(30, 41, 59, 0.3);
}
.type-buy {
  color: var(--accent-green);
}
.type-sell {
  color: var(--accent-red);
}
.type-icon {
  margin-right: 8px;
}
.action-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  color: var(--text-secondary);
  cursor: pointer;
  transition: var(--transition);
}
.action-btn:hover {
  background: var(--accent-blue);
  color: white;
}

/* Holders Container */
.holders-container {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  overflow: hidden;
  padding: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}
.holders-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.holders-title {
  font-size: 1.2rem;
  font-weight: 600;
}
.view-all {
  color: var(--accent-blue);
  text-decoration: none;
  font-weight: 500;
  transition: var(--transition);
}
.view-all:hover {
  text-decoration: underline;
}
.holders-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
}
.holder-card {
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  border-radius: 10px;
  padding: 8px 12px;
  display: flex;
  align-items: center;
  gap: 10px;
  min-height: 38px;
  transition: var(--transition);
  box-shadow: none;
}
.holder-card:hover {
  border-color: var(--accent-blue);
}
.holder-address {
  font-weight: 600;
  font-size: 0.95rem;
  flex: 1 1 40%;
  margin-bottom: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.holder-amount {
  color: var(--text-secondary);
  font-size: 0.95rem;
  flex: 1 1 35%;
  text-align: right;
}
.holder-percent {
  font-weight: 700;
  font-size: 0.95rem;
  flex: 0 0 15%;
  text-align: right;
}
.explorer-btn {
  flex: 0 0 24px;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  color: var(--accent-blue);
  cursor: pointer;
  margin-left: 8px;
  font-size: 1rem;
  transition: color 0.2s;
}
.explorer-btn:hover {
  color: var(--accent-purple);
}

/* Chat Feed */
.chat-feed {
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}
.chat-header {
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--card-border);
}
.chat-title {
  display: flex;
  align-items: center;
  gap: 10px;
}
.chat-title h2 {
  font-size: 1.3rem;
}
.token-info {
  display: flex;
  align-items: center;
  gap: 10px;
  background: rgba(30, 41, 59, 0.6);
  padding: 8px 15px;
  border-radius: 30px;
  border: 1px solid var(--card-border);
}
.token-logo {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 0.9rem;
}
.token-name {
  font-weight: 600;
  font-size: 1.1rem;
}
.chat-messages {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.message {
  display: flex;
  gap: 12px;
  max-width: 80%;
  align-self: flex-start;
}
.message.current-user {
  align-self: flex-end;
}
.user-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 0.9rem;
  flex-shrink: 0;
}
.message-content {
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.message-header {
  display: flex;
  align-items: center;
  gap: 10px;
}
.username {
  font-weight: 600;
  font-size: 0.95rem;
}
.message-time {
  color: var(--text-secondary);
  font-size: 0.8rem;
}
.message-bubble {
  background: var(--chat-bubble, #1d2a4a);
  border-radius: 18px;
  padding: 12px 16px;
  border: 1px solid var(--card-border);
  position: relative;
  max-width: 100%;
}
.message.current-user .message-bubble {
  background: var(--chat-user, #2563eb);
  border-color: rgba(37, 99, 235, 0.3);
  color: white;
}
.message-text {
  font-size: 0.95rem;
  line-height: 1.5;
  word-wrap: break-word;
}
.reactions {
  display: flex;
  gap: 8px;
  margin-top: 8px;
}
.reaction-btn {
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  border-radius: 30px;
  padding: 4px 10px;
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 0.85rem;
  cursor: pointer;
  transition: var(--transition);
}
.reaction-btn:hover {
  background: rgba(37, 99, 235, 0.2);
  border-color: var(--accent-blue);
}
.reaction-btn.active {
  background: rgba(37, 99, 235, 0.3);
  border-color: var(--accent-blue);
}
.message-actions {
  display: flex;
  gap: 15px;
  margin-top: 8px;
}
.action-btn {
  color: var(--text-secondary);
  font-size: 0.85rem;
  cursor: pointer;
  transition: var(--transition);
  background: none;
  border: none;
  padding: 0;
}
.action-btn:hover {
  color: var(--accent-blue);
}
.chat-input-container {
  padding: 15px 20px;
  border-top: 1px solid var(--card-border);
  background: var(--card-dark);
  display: flex;
  gap: 12px;
  align-items: center;
}
.emoji-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  color: var(--text-secondary);
  cursor: pointer;
  transition: var(--transition);
  flex-shrink: 0;
}
.emoji-btn:hover {
  background: var(--accent-blue);
  color: white;
}
.message-input {
  flex: 1;
  padding: 12px 18px;
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid var(--card-border);
  border-radius: 30px;
  color: var(--text-primary);
  font-size: 1rem;
  resize: none;
  height: 50px;
  max-height: 150px;
}
.message-input:focus {
  outline: none;
  border-color: var(--accent-blue);
}
.send-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--accent-blue);
  color: white;
  border: none;
  cursor: pointer;
  transition: var(--transition);
  flex-shrink: 0;
}
.send-btn:hover {
  transform: scale(1.05);
  box-shadow: var(--glow);
}
.send-btn.disabled {
  background: var(--card-border);
  cursor: not-allowed;
  opacity: 0.7;
}

/* Responsive design */
@media (max-width: 1200px) {
  .chart-panel {
    min-height: 300px;
  }
  .chart-wrapper {
    height: 300px;
  }
}

/* Mobile optimized responsive design */
@media (max-width: 1024px) {
  .swap-page-v2.mobile-optimized .container {
    padding: 10px 5px 60px;
  }
  
  .swap-page-v2.mobile-optimized .chart-panel {
    min-height: 250px;
  }
  
  .swap-page-v2.mobile-optimized .chart-wrapper {
    height: 250px;
  }
  
  .swap-page-v2.mobile-optimized .swap-panel {
    padding: 15px;
  }
  
  .swap-page-v2.mobile-optimized .token-amount {
    font-size: 1.4rem;
  }
}

@media (max-width: 992px) {
  .chart-header {
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
  }
  .chart-controls {
    flex-wrap: wrap;
    }
    .swap-panel {
        padding: 20px;
    }
}

@media (max-width: 768px) {
    .swap-page-v2 .container {
    padding: 20px 15px 80px; /* 2x footer height for proper spacing */
    padding-top: 100px; /* Ensure content starts below header on mobile */
    }
    .swap-panel {
        padding: 16px;
    }
    .token-amount {
        font-size: 1.5rem;
    }
    .slippage-modal {
        width: 250px;
        right: 10px;
    }
    .transactions-table {
        display: block;
        overflow-x: auto;
    }
    .holders-header {
        flex-direction: column;
        gap: 10px;
        align-items: flex-start;
    }
    .chat-feed {
        padding: 0;
    }
  .chart-header {
    padding: 10px 15px;
  }
  .chart-controls {
    gap: 5px;
  }
  .chart-btn {
    padding: 6px 12px;
    font-size: 0.8rem;
  }
}

@media (max-width: 576px) {
    .swap-page-v2 .container {
    padding: 15px 10px 80px; /* 2x footer height for proper spacing */
    padding-top: 100px; /* Ensure content starts below header on mobile */
    }
    .swap-panel {
        padding: 10px;
    }
    .modal-content {
        max-width: 98vw;
    }
    .transactions-container, .holders-container, .chat-feed {
        padding: 8px;
    }
    .table-header, .chat-header {
        padding: 10px;
    }
    .chat-messages {
        padding: 10px;
    }
    .chat-input-container {
        padding: 10px;
    }
  .chart-header {
    padding: 8px 12px;
  }
  .chart-controls {
    gap: 3px;
  }
  .chart-btn {
    padding: 4px 8px;
    font-size: 0.75rem;
  }
}

/* Light mode overrides */
body.light-mode, .light-mode {
  --background-dark: #f0f4f9;
  --card-dark: #ffffff;
  --card-border: #d0d8e8;
  --accent-blue: #0089ec;
  --accent-pink: #ff7bc2;
  --accent-green: #00c853;
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --glow: 0 0 15px rgba(0, 137, 236, 0.2);
  --up-color: #00c853;
  --down-color: #ff3d00;
  --chat-bubble: #e6f0ff;
  --chat-user: #0089ec;
}

body.light-mode .token-header,
body.light-mode .chart-panel,
body.light-mode .swap-panel,
body.light-mode .transactions-container,
body.light-mode .holders-container,
body.light-mode .chat-feed,
body.light-mode .token-modal,
body.light-mode .slippage-modal,
body.light-mode .tabs-dashboard {
  background: var(--card-dark) !important;
  border-color: var(--card-border) !important;
  color: var(--text-primary) !important;
}
body.light-mode .token-logo,
body.light-mode .holder-avatar,
body.light-mode .user-avatar {
  background: linear-gradient(to right, var(--accent-blue), var(--accent-pink)) !important;
}
body.light-mode .swap-btn {
  background: linear-gradient(to right, var(--accent-blue), var(--accent-pink));
}
body.light-mode .tab-btn.active {
  background: var(--accent-blue);
  color: #fff;
}
body.light-mode .transactions-table th,
body.light-mode .transactions-table tr:nth-child(even) {
  background: #f7fafd;
}
body.light-mode .message-bubble {
  background: var(--chat-bubble) !important;
  color: var(--text-primary) !important;
}
body.light-mode .message.current-user .message-bubble {
  background: var(--chat-user) !important;
  color: #fff !important;
}
body.light-mode .reaction-btn {
  background: #e6f0ff;
  border-color: var(--card-border);
}
body.light-mode .reaction-btn.active {
  background: #cce4ff;
  border-color: var(--accent-blue);
}
body.light-mode .action-btn {
  color: var(--text-secondary);
}
body.light-mode .action-btn:hover {
  color: var(--accent-blue);
}
body.light-mode .filter-btn.active, body.light-mode .filter-btn:hover {
  background: var(--accent-blue);
  color: #fff;
  border-color: var(--accent-blue);
}
body.light-mode .holder-card:hover {
  border-color: var(--accent-blue);
}
body.light-mode .swap-icon:hover {
  background: var(--accent-blue);
  color: #fff;
}
body.light-mode .slippage-option.active {
  background: var(--accent-blue);
  color: #fff;
  border-color: var(--accent-blue);
}
body.light-mode .token-selector:hover {
  background: #e6f0ff;
}
body.light-mode .token-card:hover {
  border-color: var(--accent-blue);
}
body.light-mode .modal-content {
  background: var(--card-dark);
  border-color: var(--card-border);
}
body.light-mode .search-input {
  background: #f7fafd;
  color: var(--text-primary);
}
body.light-mode .custom-slippage input {
  background: #f7fafd;
  color: var(--text-primary);
}
