.token-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  padding: 20px;
}

.token-logo {
  width: 60px;
  height: 60px;
  border-radius: 16px;
  background: linear-gradient(to right, var(--accent-blue), var(--accent-purple));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: 1.5rem;
  flex-shrink: 0;
}

.token-info {
  flex: 1;
  min-width: 0; /* Allow text to shrink */
}

.token-name {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 4px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.token-symbol {
  color: var(--text-secondary);
  font-size: 1.1rem;
  font-weight: 500;
  white-space: nowrap;
}

.token-stats {
  display: flex;
  gap: 30px;
  flex-shrink: 0;
}

.token-stat {
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.stat-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 4px;
  white-space: nowrap;
}

.stat-value {
  font-weight: 700;
  font-size: 1.2rem;
  white-space: nowrap;
}

.price-change.positive {
  color: var(--accent-green);
}

.price-change.negative {
  color: var(--accent-red);
}

/* Tablet and smaller desktop */
@media (max-width: 1200px) {
  .token-header {
    gap: 12px;
    padding: 16px;
  }
  
  .token-logo {
    width: 50px;
    height: 50px;
    font-size: 1.3rem;
  }
  
  .token-name {
    font-size: 1.3rem;
  }
  
  .token-symbol {
    font-size: 1rem;
  }
  
  .token-stats {
    gap: 20px;
  }
  
  .stat-label {
    font-size: 0.85rem;
  }
  
  .stat-value {
    font-size: 1.1rem;
  }
}

/* Medium screens */
@media (max-width: 900px) {
  .token-header {
    gap: 10px;
    padding: 12px;
  }
  
  .token-logo {
    width: 40px;
    height: 40px;
    font-size: 1.1rem;
    border-radius: 12px;
  }
  
  .token-name {
    font-size: 1.1rem;
  }
  
  .token-symbol {
    font-size: 0.9rem;
  }
  
  .token-stats {
    gap: 15px;
  }
  
  .stat-label {
    font-size: 0.8rem;
  }
  
  .stat-value {
    font-size: 1rem;
  }
}

/* Mobile */
@media (max-width: 768px) {
  .token-header {
    gap: 6px;
    padding: 8px;
  }
  
  .token-logo {
    width: 28px;
    height: 28px;
    font-size: 0.8rem;
    border-radius: 6px;
  }
  
  .token-name {
    font-size: 0.8rem;
  }
  
  .token-symbol {
    font-size: 0.7rem;
  }
  
  .token-stats {
    gap: 8px;
  }
  
  .stat-label {
    font-size: 0.6rem;
  }
  
  .stat-value {
    font-size: 0.6rem;
  }
}

/* Small mobile */
@media (max-width: 576px) {
  .token-header {
    gap: 4px;
    padding: 6px;
  }
  
  .token-logo {
    width: 24px;
    height: 24px;
    font-size: 0.7rem;
    border-radius: 4px;
  }
  
  .token-name {
    font-size: 0.7rem;
  }
  
  .token-symbol {
    font-size: 0.6rem;
  }
  
  .token-stats {
    gap: 6px;
  }
  
  .stat-label {
    font-size: 0.5rem;
  }
  
  .stat-value {
    font-size: 0.5rem;
  }
}

/* Extra small mobile */
@media (max-width: 480px) {
  .token-header {
    gap: 3px;
    padding: 4px;
  }
  
  .token-logo {
    width: 10px;
    height: 10px;
    font-size: 0.6rem;
    border-radius: 3px;
  }
  
  .token-name {
    font-size: 0.6rem;
  }
  
  .token-symbol {
    font-size: 0.5rem;
  }
  
  .token-stats {
    gap: 1px;
  }
  
  .stat-label {
    font-size: 0.45rem;
  }
  
  .stat-value {
    font-size: 0.4rem;
  }
} 