.tabs-dashboard {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  background: var(--card-dark);
  border: 1px solid var(--card-border);
  border-radius: var(--border-radius);
  padding: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}
.tab-btn {
  flex: 1 1 0;
  padding: 12px 24px;
  background: transparent;
  border: none;
  border-radius: 30px;
  color: var(--text-secondary);
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  font-size: 1rem;
}
.tab-btn.active {
  background: var(--accent-blue);
  color: white;
}
@media (max-width: 900px) {
  .tabs-dashboard {
    flex-direction: row;
    gap: 4px;
    align-items: center;
    max-width: 100%;
    border-radius: var(--border-radius);
    padding: 6px;
  }
  .tab-btn {
    width: auto;
    min-width: 0;
    padding: 8px 10px;
    font-size: 0.85rem;
    border-radius: 16px;
    text-align: center;
  }
}